import './App.css';
import React, { useState } from 'react';
import Modal from './components/Modal';
import WebApp from '@twa-dev/sdk';
import { TonConnectUIProvider, TonConnectButton } from '@tonconnect/ui-react';

function App() {
  const [isModalOpen, setIsModalOpen] = useState(false);
  const openChannel = () => {
    WebApp.openTelegramLink("https://t.me/YesCoin_Pump")
  }
  const openTwitter = () => {
    WebApp.openLink("https://x.com/5phutcrypto_")
  }
  return (
    <TonConnectUIProvider manifestUrl='https://padi-dev-tuannt1.github.io/telegram_mini_app/tonconnect-manifest.json' >

      <div className="App">
        <button onClick={() => setIsModalOpen(true)}>Open Modal</button>

        <Modal isOpen={isModalOpen} onClose={() => setIsModalOpen(false)}>
          <button onClick={() => openTwitter()}>Join twitter</button>
          <h2>This is a modal</h2>
          <p>Some content goes here.</p>
          <button onClick={() => openChannel()}>Join channel</button>
          <TonConnectButton />
        </Modal>
      </div>
    </TonConnectUIProvider>
  );
}

export default App;
